import React, { useState } from "react";
import LayoutMain from "../layouts/LayoutMain";
import { MdSave } from "react-icons/md";
import { CropImageModal } from "../components/CropImageModal";
import { Modal } from "bootstrap";

export const Profile = () => {
  const [file, setFile] = useState();
  const [croppedModalImg, setCroppedModalImg] = useState("");
  const handleChange = (e) => {
    setFile(URL.createObjectURL(e.target.files[0]));
    const showModal = new Modal("#modalDemo");
    showModal.show();
    e.preventDefault();
  };

  return (
    <>
      <LayoutMain>
        <div className="row">
          <div className="col-12 col-xl-6 col-lg-5 col-md-8 mx-auto">
            <div className="card w-auto shadow border">
              <div className="card-body">
                <div className="row">
                  <div className="d-flex flex-column align-items-center justify-content-center col-12 position-relative">
                    <label htmlFor="upload_file" style={{ cursor: "pointer" }}>
                      <div
                        className="card rounded-circle"
                        style={{
                          width: "150px",
                          height: "150px",
                          backgroundImage: `url(${croppedModalImg})`,
                          backgroundSize: "cover",
                          backgroundPosition: "center",
                          backgroundRepeat: "no-repeat",
                        }}
                      >
                        <input
                          type="file"
                          className="d-none"
                          id="upload_file"
                          onChange={handleChange}
                        />
                      </div>
                    </label>
                    <MdSave
                      className="fs-2 p-1 text-bg-primary rounded-circle position-absolute bottom-0 start-50 profile-save-icon-transform"
                      onClick={() => console.log("Save this file!")}
                    />
                  </div>
                  <CropImageModal src={file} cropDataImg={setCroppedModalImg} />
                  <div className="col-12">
                    <div className="text-center fw-bold fs-4 mt-2">
                      user@example.com
                    </div>
                    <hr />
                    <form>
                      <div className="input-group mb-3">
                        <span
                          className="input-group-text bg-transparent"
                          id="basic-addon1"
                        >
                          <i className="bi bi-person-fill"></i>
                        </span>
                        <input
                          type="text"
                          className="form-control border-start-0 shadow-none"
                          placeholder="Full Name"
                          aria-label="FullName"
                          aria-describedby="basic-addon1"
                        />
                      </div>
                      <div className="input-group mb-3">
                        <span
                          className="input-group-text bg-transparent"
                          id="basic-addon1"
                        >
                          <i className="bi bi-phone"></i>
                        </span>
                        <input
                          type="text"
                          className="form-control border-start-0 shadow-none"
                          placeholder="Mobile"
                          aria-label="Mobile"
                          aria-describedby="basic-addon1"
                        />
                      </div>
                      <div className="mb-3 text-end">
                        <button className="btn btn-sm btn-danger">
                          <MdSave /> Update
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutMain>
    </>
  );
};
