import { createContext, useContext } from "react"
import { useLocalStorage } from "../hooks/useLocalStorage"
import toast from "react-hot-toast";
import {ToasterList} from "../data/ToasterList";

const notify_cart_add = () => toast(ToasterList.cart_add.message, ToasterList.cart_add.options);
const notify_cart_remove = () => toast(ToasterList.cart_remove.message, ToasterList.cart_remove.options);
const notify_cart_delete = () => toast(ToasterList.cart_delete.message, ToasterList.cart_delete.options);

const ShoppingCartContext = createContext({})

export const useShoppingCart = () => {
    return useContext(ShoppingCartContext)
}

export const ShoppingCartProvider = ({children}) => {
    const [cartItems, setCartItems] = useLocalStorage("shopping-cart", [])

    const cartQuantity = cartItems.reduce(
        (quantity, item) => (item.quantity + quantity) >= 100 ? "99+": item.quantity + quantity,
        0
    )

    const getItemQuantity = (id) => {
        return cartItems.find(item => item.id === id)?.quantity || 0
    }
    const incrementCartQuantity = (id) => {
        setCartItems(currItems => {
            if(currItems.find(item => item.id === id) == null) {
                return [...currItems, { id, quantity: 1}]
            }else{
                return currItems.map(item => {
                    if(item.id === id){
                        return { ...item, quantity: item.quantity + 1 }
                    }else{
                        return item
                    }
                })
            }
        });
        notify_cart_add();
    }
    const decrementCartQuantity = (id) => {
        setCartItems(currItems => {
            if(currItems.find(item => item.id === id)?.quantity === 1){
                return currItems.filter(item => item.id !== id)
            }else{
                return currItems.map(item => {
                    if(item.id === id){
                        return { ...item, quantity: item.quantity - 1 }
                    }else{
                        return item
                    }
                })
            }
        });
        notify_cart_remove();
    }
    const removeFromCart = (id) => {
        setCartItems(currItems => {
            return currItems.filter(item => item.id !== id)
        });
        notify_cart_delete();
    }

    return <ShoppingCartContext.Provider value={{
        getItemQuantity,
        incrementCartQuantity,
        decrementCartQuantity,
        removeFromCart,
        cartItems,
        cartQuantity
    }}>
        {children}
    </ShoppingCartContext.Provider>
}