import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode } from "swiper";
import "swiper/css/bundle";

import { Link } from "react-router-dom";

import { MdLunchDining } from "react-icons/md";
import { MdLocalPizza } from "react-icons/md";
import { MdFastfood } from "react-icons/md";
import { MdLocalCafe } from "react-icons/md";
import { MdLocalDining } from "react-icons/md";
import "hover.css/css/hover.css";

export const SlideSwiperBar = () => {
  return (
    <>
      <Swiper
        className="mt-3"
        slidesPerView={"auto"}
        spaceBetween={10}
        modules={[FreeMode]}
        freeMode={true}
      >
        <SwiperSlide>
          <Link to="/">
            <div className="card text-center mb-3 hvr-underline-from-center border shadow-sm">
              <div className="card-body">
                <h5 className="card-title">
                  <MdLunchDining className="fs-2 text-danger" />
                </h5>
                <p className="card-text">Lunch</p>
              </div>
            </div>
          </Link>
        </SwiperSlide>
        <SwiperSlide>
          <Link to="/">
            <div className="card text-center mb-3 hvr-underline-from-center border shadow-sm">
              <div className="card-body">
                <h5 className="card-title">
                  <MdLocalPizza className="fs-2 text-success" />
                </h5>
                <p className="card-text">Pizza</p>
              </div>
            </div>
          </Link>
        </SwiperSlide>
        <SwiperSlide>
          <Link to="/">
            <div className="card text-center mb-3 hvr-underline-from-center border shadow-sm">
              <div className="card-body">
                <h5 className="card-title">
                  <MdFastfood className="fs-2 text-primary" />
                </h5>
                <p className="card-text">Fast food</p>
              </div>
            </div>
          </Link>
        </SwiperSlide>
        <SwiperSlide>
          <Link to="/">
            <div className="card text-center mb-3 hvr-underline-from-center border shadow-sm">
              <div className="card-body">
                <h5 className="card-title">
                  <MdLocalCafe className="fs-2 text-warning" />
                </h5>
                <p className="card-text">Cafe</p>
              </div>
            </div>
          </Link>
        </SwiperSlide>
        <SwiperSlide>
          <Link to="/">
            <div className="card text-center mb-3 hvr-underline-from-center border shadow-sm">
              <div className="card-body">
                <h5 className="card-title">
                  <MdLocalDining className="fs-2 text-info" />
                </h5>
                <p className="card-text">Dining</p>
              </div>
            </div>
          </Link>
        </SwiperSlide>
      </Swiper>
    </>
  );
};
