import LayoutMain from "../layouts/LayoutMain";
import { ItemsDataList } from "../data/ItemsDataList";
import { useShoppingCart } from "../context/ShoppingCartContext";
import { CartItems } from "../components/CartItems";
import { formatFraction } from "../utilities/formatCurrency";
import { Link } from "react-router-dom";

export const Cart = () => {
  const { cartItems } = useShoppingCart();

  return (
    <>
      <LayoutMain>
        {cartItems.length > 0 ? (
          <>
            {cartItems.map((item) => (
              <CartItems key={item.id} {...item} />
            ))}
            <div className="cart-divider my-3"></div>
            <div className="card py-3 px-2 border-0 rounded-3">
              <div className="d-flex flex-row align-items-center justify-content-start fw-bold fs-5">
                <div>Total</div>
                <div className="ms-auto text-info-emphasis">
                  <span className="cart-fs-small">RM </span>
                  {formatFraction(
                    cartItems.reduce((total, cartItem) => {
                      const item = ItemsDataList.find(
                        (i) => i.id === cartItem.id
                      );
                      return total + (item?.cost || 0) * cartItem.quantity;
                    }, 0)
                  )}
                </div>
              </div>
            </div>
          </>
        ) : (
          <>
            <div className="d-flex flex-column align-items-center justify-content-center mt-5">
              <i className="bi bi-cart2 fs-1 fw-blod"></i>
              <div className="display-5">Your cart is empty..</div>
              <div className="text-muted text-center mb-4">
                Start by adding some items to your cart and come back!
              </div>
              <Link
                to="/"
                className="text-decoration-none btn btn-md btn-primary"
              >
                <i className="bi bi-house-fill"></i> Home
              </Link>
            </div>
          </>
        )}
      </LayoutMain>
    </>
  );
};
