import React, { Suspense } from "react";
import LayoutMain from "../layouts/LayoutMain";
import { SlideSwiperBar } from "../components/SlideSwiperBar";
import { MdViewAgenda } from "react-icons/md";
import { MdGridView } from "react-icons/md";
import { ItemsDataList } from "../data/ItemsDataList";
import { ItemCardGridView } from "../components/ItemCardGridView";
import { ItemCardListView } from "../components/ItemCardListView";
import { useLocalStorage } from "../hooks/useLocalStorage";
// import { SlideBannersBar } from "../components/SlideBannersBar";
const LazySlideBannersBar = React.lazy(() =>
  import("../components/SlideBannersBar")
);
// const LazySlideBannersBar = React.lazy(
//   () => import('../components/SlideBannersBar').then(module => ({ default: module.SlideBannersBar }))
// );

export const Home = () => {
  const [viewType, setViewType] = useLocalStorage("menuView");
  const handelViewType = (userViewType) => {
    setViewType(userViewType);
  };
  return (
    <>
      <LayoutMain>
        <div className="input-group mb-3">
          <input
            type="text"
            className="form-control shadow-none"
            placeholder="Search ..."
            aria-label="Search ..."
            aria-describedby="button-addon2"
          />
          <button
            className="btn bg-transparent border"
            type="button"
            id="button-addon2"
          >
            <i className="bi bi-search text-primary"></i>
          </button>
        </div>
        <SlideSwiperBar />
        <Suspense
          fallback={
            <div
              className="btn btn-secondary disabled placeholder rounded-3"
              style={{ width: "100%", height: "150px" }}
            ></div>
          }
        >
          <LazySlideBannersBar />
        </Suspense>
        <div className="d-flex flex-row align-items-center justify-content-between py-2">
          <h4>Menu</h4>
          <div>
            <div className="btn-group btn-group-sm">
              {viewType === "list" ? (
                <MdViewAgenda
                  className="fs-2"
                  onClick={() => handelViewType("grid")}
                />
              ) : (
                <MdGridView
                  className="fs-2"
                  onClick={() => handelViewType("list")}
                />
              )}
            </div>
            <div className="btn-group btn-group-sm"></div>
          </div>
        </div>
        {viewType === "list" ? (
          <div className="row row-cols-1 row-cols-lg-4 g-3">
            {ItemsDataList.map((item) => (
              <div className="col" key={item.id}>
                <ItemCardGridView item={item} />
              </div>
            ))}
          </div>
        ) : (
          <div className="row row-cols-1 row-cols-sm-2 row-cols-md-2 row-cols-xl-4 g-3">
            {ItemsDataList.map((item) => (
              <div className="col" key={item.id}>
                <ItemCardListView item={item} />
              </div>
            ))}
          </div>
        )}
      </LayoutMain>
    </>
  );
};
