import { Link } from "react-router-dom";
import { useShoppingCart } from "../context/ShoppingCartContext";
import { formatFraction } from "../utilities/formatCurrency";
import "hover.css/css/hover.css";

export const ItemCardGridView = ({ item }) => {
  const { incrementCartQuantity } = useShoppingCart();
  return (
    <>
      <div className="card shadow-sm border">
        <Link
          to={"/item/" + item?.id}
          className="text-decoration-none link-bg-dark"
        >
          <div className="ratio ratio-4x3">
            <img src={item?.image} alt={item?.name} className="card-img-top" />
          </div>
          <div className="card-body pb-0 text-info-emphasis">
            <span className="d-inline-block mw-100 text-truncate">
              {item?.name}
            </span>
            <div className="text-muted cart-fs-small">{item?.category}</div>
          </div>
        </Link>
        <div className="card-body d-flex flex-row align-items-center justify-content-between pt-2">
          <small className="text-info-emphasis">
            <span className="cart-fs-small">RM </span>
            {formatFraction(item.cost)}
          </small>
          <i
            className="bi bi-plus-circle-fill text-warning-emphasis fs-4 pointer hvr-grow"
            onClick={() => incrementCartQuantity(item?.id)}
          ></i>
        </div>
      </div>
    </>
  );
};
