import { useState } from "react";
import LayoutMain from "../layouts/LayoutMain";
import { useParams } from "react-router-dom";
import { ItemsDataList } from "../data/ItemsDataList";
import { MdShoppingCart } from "react-icons/md";
import { MdRateReview } from "react-icons/md";

export const Item = () => {
  const itemId = useParams()?.id;
  const itemData = ItemsDataList.filter((item) => item.id === parseInt(itemId));

  const [rating, setRating] = useState(0);
  const ratingClass = "me-1 bi bi-star";
  const ratingClassOver = ratingClass + "-fill text-warning";

  const HandelRating = (ratingNumber) => {
    setRating(ratingNumber);
  };

  const [favoriteIcon, setFavoriteIcon] = useState(false);

  return (
    <>
      <LayoutMain>
        {itemData.length > 0 ? (
          <>
            <div className="card shadow-sm border mb-2">
              <div
                className="img-background rounded-top position-relative"
                style={{ backgroundImage: `url(${itemData[0]?.image})` }}
              >
                <div
                  className="position-absolute bottom-0 end-0 mb-3 me-4 bg-white rounded-circle p-1"
                  onClick={() =>
                    setFavoriteIcon((favoriteIcon) => !favoriteIcon)
                  }
                >
                  <i
                    className={`bi bi-heart${
                      favoriteIcon === true ? "-fill" : ""
                    } p-1 text-danger`}
                  ></i>
                </div>
              </div>
              <div className="card-body pb-0">
                <span className="fs-120 fw-bold d-inline-block mw-100 text-truncate">
                  {itemData[0]?.name}
                </span>
                <div className="small">{itemData[0]?.category}</div>
              </div>
              <div className="card-body d-flex flex-row align-items-center justify-content-between py-2">
                <span className="fs-5 fw-500">
                  <small>RM</small> <strong>{itemData[0]?.cost}</strong>
                </span>
                <button className="btn btn-sm btn-primary">
                  <MdShoppingCart className="fs-3" /> Add to cart
                </button>
              </div>
              <div className="card-body py-3 text-justify">
                <div>
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure,
                  repellendus consequatur! Maxime eligendi provident magni
                  aliquam? A accusantium voluptatibus, incidunt distinctio eius
                  officiis aperiam enim quam ea repudiandae! Facilis ex cumque
                  voluptatum aut dolore provident quisquam doloremque. Eaque,
                  vero et veniam iste ipsum numquam vitae natus nam vel non ab
                  magni sit, illum deleniti nemo architecto, nisi esse
                  reprehenderit molestiae accusamus similique rerum aperiam
                  quasi. Necessitatibus.
                </div>
              </div>
            </div>
            <div className="card shadow-sm border mb-2">
              <div className="card-body py-3 text-justify border-bottom">
                <div className="display-6 card-title border-bottom">
                  Reviews
                </div>
                <div className="d-flex flex-row align-items-center justify-content-start mb-1">
                  <i
                    className={rating >= 1 ? ratingClassOver : ratingClass}
                    onMouseEnter={() => HandelRating(1)}
                  ></i>
                  <i
                    className={rating >= 2 ? ratingClassOver : ratingClass}
                    onMouseEnter={() => HandelRating(2)}
                  ></i>
                  <i
                    className={rating >= 3 ? ratingClassOver : ratingClass}
                    onMouseEnter={() => HandelRating(3)}
                  ></i>
                  <i
                    className={rating >= 4 ? ratingClassOver : ratingClass}
                    onMouseEnter={() => HandelRating(4)}
                  ></i>
                  <i
                    className={rating >= 5 ? ratingClassOver : ratingClass}
                    onMouseEnter={() => HandelRating(5)}
                  ></i>
                </div>
                <textarea className="form-control mb-2"></textarea>
                <div className="d-grid gap-2 justify-content-end">
                  <button className="btn btn-sm btn-success">
                    <MdRateReview /> Submit
                  </button>
                </div>
              </div>
              <div className="card-body py-2 text-justify border-bottom">
                <small className="me-auto">
                  User Name
                  <br />
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <br />
                  <small>2022-12-31 11:30 PM</small>
                </small>
                <div>Test message!</div>
              </div>
              <div className="card-body py-2 text-justify border-bottom">
                <small className="me-auto">
                  User Name
                  <br />
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star"></i>
                  <br />
                  <small>2022-12-31 11:30 PM</small>
                </small>
                <div>Test message!</div>
              </div>
              <div className="card-body py-2 text-justify border-bottom">
                <small className="me-auto">
                  User Name
                  <br />
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star-fill text-warning"></i>
                  <i className="bi bi-star"></i>
                  <i className="bi bi-star"></i>
                  <i className="bi bi-star"></i>
                  <br />
                  <small>2022-12-31 11:30 PM</small>
                </small>
                <div>Test message!</div>
              </div>
            </div>
          </>
        ) : (
          <h1>No data!</h1>
        )}
      </LayoutMain>
    </>
  );
};
