import { Link } from "react-router-dom";
import { useShoppingCart } from "../context/ShoppingCartContext";
import { formatFraction } from "../utilities/formatCurrency";
import "hover.css/css/hover.css";

export const ItemCardListView = ({ item }) => {
  const { incrementCartQuantity } = useShoppingCart();
  return (
    <>
      <div className="card border-0 rounded-3">
        <div className="d-flex shadow-sm align-items-center">
          <Link to={"/item/" + item?.id} className="text-decoration-none">
            <div className="flex-shrink-0 ps-2 py-2">
              <div
                className="img-background-card-80 rounded position-relative"
                style={{
                  backgroundImage: `url(${item?.image})`,
                }}
              ></div>
            </div>
          </Link>
          <div className="flex-grow-1 text-truncate p-2">
            <Link to={"/item/" + item?.id} className="text-decoration-none">
              <span className="d-inline-block mw-100 text-truncate text-info-emphasis">
                {item?.name}
              </span>
              <div className="text-muted cart-fs-small">{item?.category}</div>
            </Link>
            <div className="fw-500">
              <div className="d-flex flex-row">
                <div>
                  <small className="fw-semibold text-info-emphasis">
                    <span className="cart-fs-small">RM </span>
                    {formatFraction(item?.cost)}{" "}
                    <span className="text-muted cart-fs-small"> </span>
                  </small>
                </div>
                <div className="ms-auto">
                  <i
                    className="bi bi-plus-circle-fill text-warning-emphasis fs-4 pointer hvr-grow"
                    onClick={() => incrementCartQuantity(item.id)}
                  ></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
