import { ItemsDataList } from "../data/ItemsDataList";
import { useShoppingCart } from "../context/ShoppingCartContext";
import { formatFraction } from "../utilities/formatCurrency";

export const CartItems = ({ id, quantity }) => {
  const { incrementCartQuantity, decrementCartQuantity, removeFromCart } =
    useShoppingCart();

  const item = ItemsDataList.find((i) => i.id === id);
  if (item == null) return null;

  return (
    <div className="card border-0 rounded-3">
      <div className="d-flex p-2 border-bottom align-items-center">
        <div className="pe-2 flex-shrink-0">
          <div
            className="img-background-card-80 rounded position-relative"
            style={{
              backgroundImage: `url(${item?.image})`,
            }}
          ></div>
        </div>
        <div className="flex-grow-1 text-truncate">
          {item?.name}
          <div className="fw-500">
            <div className="d-flex flex-row">
              <div>
                <small className="fw-semibold">
                  <span className="cart-fs-small">RM </span>
                  {formatFraction(item?.cost)}{" "}
                  <span className="text-muted cart-fs-small">
                    {" "}
                    x {quantity >= 1 && quantity}
                  </span>
                </small>
              </div>
              <div className="ms-auto">
                <i
                  className="bi bi-trash3 text-danger pointer"
                  onClick={() => removeFromCart(item?.id)}
                ></i>
              </div>
            </div>
          </div>
          <div className="d-flex flex-row">
            <i
              className="bi bi-plus-circle-fill text-warning-emphasis fs-4 pointer"
              onClick={() => incrementCartQuantity(item.id)}
            ></i>
            <div className="align-baseline text-center my-1 px-1 cart-count-value">
              {quantity >= 1 && quantity}
            </div>
            <i
              className="bi bi-dash-circle-fill text-warning-emphasis fs-4 pointer"
              onClick={() => decrementCartQuantity(item.id)}
            ></i>
            <div className="ms-auto text-info-emphasis align-self-end">
              <small>
                <span className="cart-fs-small">RM </span>
                {formatFraction(item.cost * quantity)}
              </small>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
