import LayoutMain from "../layouts/LayoutMain";
import { OrdersDataList } from "../data/OrdersDataList";

const orser_status_color = {
  new: "primary",
  pending: "warning",
  done: "success",
};
export const Orders = () => {
  return (
    <>
      <LayoutMain>
        {OrdersDataList.map((order) => (
          <div className="card border-0 rounded-0 mb-3" key={order.order_id}>
            <div className="d-flex flex-row align-items-center justify-content-between p-2 border-bottom">
              <div className="fw-bold">
                Order ID <span className="text-info">#{order.order_id}</span>
              </div>
              <div className="text-end text-capitalize small">
                <div
                  className={`text-${
                    orser_status_color[order.order_status.toLowerCase()]
                  } small`}
                >
                  <i className="bi bi-circle-fill"></i> {order.order_status}
                </div>
                <div>
                  <span className="small">{order.order_date}</span>
                </div>
              </div>
            </div>
            {order.items_list.length > 0 ? (
              order.items_list.map((item_array) => (
                <div
                  className="d-flex p-2 border-bottom align-items-center"
                  key={item_array.id}
                >
                  <div className="pe-2 flex-shrink-0">
                    <div
                      className="img-background-card-60 rounded position-relative"
                      style={{
                        backgroundImage: `url(${item_array.image})`,
                      }}
                    ></div>
                  </div>
                  <div className="flex-grow-1 text-truncate">
                    {item_array.name}
                    <div className="d-flex flex-row">
                      <div>
                        <small className="fw-semibold">
                          <span className="cart-fs-small">RM </span>
                          {item_array.cost}
                        </small>
                      </div>
                      <div className="ms-auto">
                        <span className="text-muted cart-fs-small">
                          {item_array.quantity}x
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <h1>No items found in order!</h1>
            )}
          </div>
        ))}
      </LayoutMain>
    </>
  );
};
