export const NoMatch = () => {
  return (
    <>
      <div
        className="modal fade show z-0"
        id="exampleModalCenter"
        aria-labelledby="exampleModalCenterTitle"
        style={{ display: "block" }}
        aria-modal="true"
        role="dialog"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content bg-transparent border-0">
            <div className="modal-body">
              <div className="d-flex flex-column align-items-center justify-content-center animate__animated animate__bounce">
                <div className="col">
                  <div className="">
                    <div className="snow">
                      <span style={{ "--i": 11 }}></span>
                      <span style={{ "--i": 12 }}></span>
                      <span style={{ "--i": 15 }}></span>
                      <span style={{ "--i": 17 }}></span>
                      <span style={{ "--i": 18 }}></span>
                      <span style={{ "--i": 13 }}></span>
                      <span style={{ "--i": 14 }}></span>
                      <span style={{ "--i": 19 }}></span>
                      <span style={{ "--i": 20 }}></span>
                      <span style={{ "--i": 10 }}></span>
                      <span style={{ "--i": 18 }}></span>
                      <span style={{ "--i": 13 }}></span>
                      <span style={{ "--i": 14 }}></span>
                      <span style={{ "--i": 19 }}></span>
                      <span style={{ "--i": 20 }}></span>
                      <span style={{ "--i": 10 }}></span>
                      <span style={{ "--i": 18 }}></span>
                      <span style={{ "--i": 13 }}></span>
                      <span style={{ "--i": 14 }}></span>
                      <span style={{ "--i": 19 }}></span>
                      <span style={{ "--i": 20 }}></span>
                      <span style={{ "--i": 10 }}></span>
                    </div>
                  </div>
                </div>
                <div className="col">
                  <h1 className="display-1 fw-bold text-shadow">404</h1>
                </div>
                <div className="col">
                  <h2 className="display-5 fw-bold text-shadow">
                    Page Not Found!
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
